import Vue from 'vue';
import Router from 'vue-router';
import { weChatShare } from '$wechat';
import { useInVueRouter } from '$use-in-vue-router';
import RouterID from '$use-in-vue-router/router-id';
import { routerBeforeEach } from "$use-in-vue-router/router-before";
import { routerAfterEach } from "$use-in-vue-router/router-after";
import { useInVueRouterProgram } from '../use-in-vue-router-program';

//
import routes from './routes';

Vue.use(Router);

//router
useInVueRouter(Router);

//
export const routerID = new RouterID();

//
const router = new Router({
  namespace: true,
  routes: routerID.addID(routes)
});

//router
useInVueRouterProgram({
  Router,
  routerID
});

//router before each
routerBeforeEach({
  router,
  beforeEach(to, from) {
    //pathfalseErrornext
    // true  undefined next()
    //nextRouterNext
    return true;
  }
});

//router after each
routerAfterEach({
  router,
  //after each
  afterEach(to, from) {
    //console.log(to, from);
  },
  unAfterHook() {
    weChatShare();
  }
});

export default router;
