//webview
class NativeApp {
  constructor() {
    //
    this.tasks = [];
  }

  addTask(fn) {
    if (typeof fn !== 'function') return;
    this.tasks.push(fn);
  }
}

/**/
NativeApp.prototype.take = //
  NativeApp.prototype.task = function () {
    while (this.tasks.length !== 0) {
      try {
        this.tasks.shift().call(this);
      } catch (e) {
        console.warn(e);
      }
    }
  };

export default NativeApp;