import router from '@router';

const homeTabBar = {
  list: {
    items: [{
      content: {
        value: '',
        fontSize: 10
      },
      icon: {
        src: 'https://www.dtb315.com/Static/wap/home/images/bottom_btn/home.png',
        activeSrc: 'https://www.dtb315.com/Static/wap/home/images/bottom_btn/home-active.png',
        style: 'width:20px;height:20px;',
        direction: 'top'
      },
      to: '/',
      className: 'bc-pd-5 bc-bd-r-e5e'
    }, {
      content: {
        value: '',
        fontSize: 10
      },
      icon: {
        src: 'https://www.dtb315.com/Static/wap/home/images/bottom_btn/home.png',
        activeSrc: 'https://www.dtb315.com/Static/wap/home/images/bottom_btn/home-active.png',
        style: 'width:20px;height:20px;',
        direction: 'bottom'
      },
      to: '/',
      className: 'bc-pd-5 bc-bd-r-e5e',
      fontSize: 10
    }, {
      content: {
        value: '',
        fontSize: 10
      },
      icon: {
        src: 'https://www.dtb315.com/Static/wap/home/images/bottom_btn/allclass.png',                       //src
        activeSrc: 'https://www.dtb315.com/Static/wap/home/images/bottom_btn/allclass-active.png',
        direction: 'left',
        style: 'width:20px;height:20px;'
      },
      to: '/components',
      className: 'bc-pd-5'
    }]
  },
  active() {
    if (router.matchRoutes([
        /^\/component.*/,    //
      ])) {
      this.activeIndex = 1;
    } else {
      this.activeIndex = 0;
    }
  }
};

export default homeTabBar;