function inBrowser() {
  return process.client || process.browser;
}

//host
let host = `localhost`;

// ssr 
if (inBrowser()) {
  //
  host = location.host;
}

//
const dev = (() => /localhost|192\.168|127\.0\.0\.1|pc\./ig.test(host))();

//beta
const beta = (() => (!dev) && (/beta\./ig.test(host)))();

//
const prod = (() => (!dev && !beta))();

module.exports = {
  dev,
  beta,
  prod
};